/* eslint-disable max-classes-per-file */
/* eslint-disable react/no-unused-state */
import * as React from 'react';
import Paper from '@material-ui/core/Paper';
import {
  Scheduler,
  AppointmentForm,
} from '@devexpress/dx-react-scheduler-material-ui';
import { connectProps } from '@devexpress/dx-react-core';
import { KeyboardDateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import EventIcon from '@material-ui/icons/Event';
import Typography from '@material-ui/core/Typography';
import yellow from '@material-ui/core/colors/yellow';
import Grid from '@material-ui/core/Grid';

import CircularProgress from '@material-ui/core/CircularProgress';
import { green } from '@material-ui/core/colors';
import AcUnitIcon from '@material-ui/icons/AcUnit';

const containerStyles = theme => ({
  container: {
    width: theme.spacing(68),
    padding: 0,
    paddingBottom: theme.spacing(2),
  },
  content: {
    padding: theme.spacing(1),
    paddingTop: 0,
  },
  header: {
    overflow: 'hidden',
    paddingTop: theme.spacing(0.5),
  },
  buttonGroup: {
    display: 'flex',
    justifyContent: 'center',
    padding: theme.spacing(4, 2),
  },
  button: {
    marginLeft: theme.spacing(0),
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
  picker: {
    margin: theme.spacing(2),
    '&:last-child': {
      marginRight: 0,
      '.MuiIconButton-label':{
        backgroundColor: 'blue'
      }
    },
    width: '90%',
  },
  wrapper: {
    padding: theme.spacing(5, 0, 0, 0),
  },
});

class AppointmentFormContainerBasic extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      appointmentChanges: {},
      button: true,
    };

    this.getAppointmentData = () => {
      const { appointmentData } = this.props;
      return appointmentData;
    };
    this.getAppointmentChanges = () => {
      const { appointmentChanges } = this.state;
      return appointmentChanges;
    };
    this.isDisbled = true;
    this.changeAppointment = this.changeAppointment.bind(this);
    this.commitAppointment = this.commitAppointment.bind(this);
  }

  componentDidUpdate(){
    if(this.state.appointmentChanges.startDate && this.state.appointmentChanges.endDate){
      this.setState({...this.state.appointmentChanges, button: this.props.loading})
    }
  }

  changeAppointment({ field, changes }) {
    const nextChanges = {
      ...this.getAppointmentChanges(),
      [field]: changes,
    };
    this.setState({
      appointmentChanges: nextChanges,
    });
  }

  commitAppointment(type) {
    const { commitChanges } = this.props;
    const appointment = {
      ...this.getAppointmentData(),
      ...this.getAppointmentChanges(),
    };
    if (type === 'deleted') {
      commitChanges({ [type]: appointment.id });
    } else if (type === 'changed') {
      commitChanges({ [type]: { [appointment.id]: appointment } });
    } else {
      commitChanges({ [type]: appointment });
    }
    this.setState({
      appointmentChanges: {},
    });
  }

  setDateTime () {
    this.props.setDateTime(this.state.appointmentChanges)
  }

  render() {
    const {
      classes,
      appointmentData,
      loading
    } = this.props;
    const { appointmentChanges } = this.state;

    const displayAppointmentData = {
      ...appointmentData,
      ...appointmentChanges,
    };

   const pickerEditorProps = field => ({
      className: classes.picker,
      // keyboard: true,
      ampm: false,
      value: displayAppointmentData[field],
      onChange: date => this.changeAppointment({
        field: [field], changes: date ? date.toDate() : new Date(displayAppointmentData[field]),
      }),
      inputVariant: 'outlined',
      format: 'DD/MM/YYYY HH:mm',
      onError: () => null,
      defaultValue: "Success"
    });

    return (
        <div>
          <div className={classes.content}>
            <div className={classes.wrapper}>
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <Grid container direction="row" justify="center">
                  <Grid item xs={12} sm={12} md={6}>
                    <KeyboardDateTimePicker
                       label="Date du début"
                       required
                       {...pickerEditorProps('startDate')}
                     />
                   </Grid>
                   <Grid item xs={12} sm={12} md={6}>
                     <KeyboardDateTimePicker
                        label="Date de la fin"
                        required
                        {...pickerEditorProps('endDate')}
                    />
                  </Grid>
                </Grid>
              </MuiPickersUtilsProvider>
            </div>
          </div>
          <div className={classes.buttonGroup}>
            <Button
              onClick={this.setDateTime.bind(this)}
              fullWidth
              variant="contained"
              color="primary"
              disabled={this.state.button}
              className={classes.button}
            >{loading && <CircularProgress size={24} className={classes.buttonProgress} />}
              Reserver
            </Button>
          </div>
        </div>
    );
  }
}

const AppointmentFormContainer = withStyles(containerStyles, { name: 'AppointmentFormContainer' })(AppointmentFormContainerBasic);

const styles = theme => ({
  avatar: {
    height: 70,
    width: 70,
    margin: theme.spacing(1),
    backgroundColor: yellow[500],
    color: theme.palette.primary.main,
    boxShadow: "0 2px 3px 0 rgba(0, 0, 0, 0.2)",
  },
  title: {
    textAlign: 'center',
    marginBottom: theme.spacing(2)
  }
});

/* eslint-disable-next-line react/no-multi-comp */
class Demo extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      currentDate: '2018-06-27',
      confirmationVisible: false,
      editingFormVisible: true,
      deletedAppointmentId: undefined,
      editingAppointment: undefined,
      previousAppointment: undefined,
      addedAppointment: {},
      startDayHour: 9,
      endDayHour: 19,
      isNewAppointment: false,
    };

    this.toggleConfirmationVisible = this.toggleConfirmationVisible.bind(this);
    this.commitDeletedAppointment = this.commitDeletedAppointment.bind(this);
    this.toggleEditingFormVisibility = this.toggleEditingFormVisibility.bind(this);

    this.commitChanges = this.commitChanges.bind(this);
    this.onEditingAppointmentChange = this.onEditingAppointmentChange.bind(this);
    this.onAddedAppointmentChange = this.onAddedAppointmentChange.bind(this);
    this.appointmentForm = connectProps(AppointmentFormContainer, () => {
      const {
        editingFormVisible,
        editingAppointment,
        data,
        addedAppointment,
        isNewAppointment,
        previousAppointment,
      } = this.state;

      const currentAppointment = data
        .filter(appointment => editingAppointment && appointment.id === editingAppointment.id)[0]
        || addedAppointment;
      const cancelAppointment = () => {
        if (isNewAppointment) {
          this.setState({
            editingAppointment: previousAppointment,
            isNewAppointment: false,
          });
        }
      };

      return {
        visible: editingFormVisible,
        appointmentData: currentAppointment,
        commitChanges: this.commitChanges,
        setDateTime: this.props.setDateTime,
        loading: this.props.loading,
        visibleChange: this.toggleEditingFormVisibility,
        onEditingAppointmentChange: this.onEditingAppointmentChange,
        cancelAppointment,
      };
    });
  }

  componentDidUpdate() {
    this.appointmentForm.update();
  }

  onEditingAppointmentChange(editingAppointment) {
    this.setState({ editingAppointment });
  }

  onAddedAppointmentChange(addedAppointment) {
    this.setState({ addedAppointment });
    const { editingAppointment } = this.state;
    if (editingAppointment !== undefined) {
      this.setState({
        previousAppointment: editingAppointment,
      });
    }
    this.setState({ editingAppointment: undefined, isNewAppointment: true });
  }

  setDeletedAppointmentId(id) {
    this.setState({ deletedAppointmentId: id });
  }

  toggleEditingFormVisibility() {
    const { editingFormVisible } = this.state;
    this.setState({
      editingFormVisible: !editingFormVisible,
    });
  }

  toggleConfirmationVisible() {
    const { confirmationVisible } = this.state;
    this.setState({ confirmationVisible: !confirmationVisible });
  }

  commitDeletedAppointment() {
    this.setState((state) => {
      const { data, deletedAppointmentId } = state;
      const nextData = data.filter(appointment => appointment.id !== deletedAppointmentId);

      return { data: nextData, deletedAppointmentId: null };
    });
    this.toggleConfirmationVisible();
  }

  commitChanges({ added, changed, deleted }) {
    this.setState((state) => {
      let { data } = state;
      if (added) {
        const startingAddedId = data.length > 0 ? data[data.length - 1].id + 1 : 0;
        data = [...data, { id: startingAddedId, ...added }];
      }
      if (changed) {
        data = data.map(appointment => (
          changed[appointment.id] ? { ...appointment, ...changed[appointment.id] } : appointment));
      }
      if (deleted !== undefined) {
        this.setDeletedAppointmentId(deleted);
        this.toggleConfirmationVisible();
      }
      return { data, addedAppointment: {} };
    });
  }

  render() {
    const {
      data
    } = this.state;
    const { classes } = this.props;

    return (
      <>          
        <Avatar className={classes.avatar} >
          <EventIcon  fontSize="large"/>
        </Avatar>
       <Typography className={classes.title} color='primary' component="h1" variant="h5">
          Selectionner la période de location
       </Typography>

      <Paper>
        <Scheduler
          data={data}
        >
          <AppointmentForm
            overlayComponent={this.appointmentForm}
            visible={true}
          />
        </Scheduler>
      </Paper>
      </>
    );
  }
}

export default withStyles(styles, { name: 'EditingDemo' })(Demo);