
import React, { useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import { green } from '@material-ui/core/colors';

import { useHistory, useParams } from 'react-router-dom';
import axiosInstance from 'axios';

import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

import Button from "components/CustomButtons/Button.js";
import { Link } from "react-router-dom";
import Divider from '@material-ui/core/Divider';
import DoneAllRoundedIcon from '@material-ui/icons/DoneAllRounded';
import AcUnitRoundedIcon from '@material-ui/icons/AcUnitRounded';
import EuroSymbolRoundedIcon from '@material-ui/icons/EuroSymbolRounded';
import CardGiftcardIcon from '@material-ui/icons/CardGiftcard';
import NewReleasesRoundedIcon from '@material-ui/icons/NewReleasesRounded';

import DateTimePicker from './DateTimePicker.jsx';
import Login from 'components/Login.jsx';
import CircularProgress from '@material-ui/core/CircularProgress';


const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    marginTop: 70,
    backgroundColor: 'ddd',
    color: 'black',
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
  header:{
    height: '100%',
  },
  paper: {
    margin: theme.spacing(13, 15),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    "@media (max-width: 560px)": {
      margin: theme.spacing(2, 2, 10, 2),
    }
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  //preload
  wrapper: {
    margin: theme.spacing(1),
    position: 'relative',
  },
  buttonSuccess: {
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[700],
    },
  },
  fabProgress: {
    color: green[500],
    position: 'absolute',
    top: -6,
    left: -6,
    zIndex: 1,
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
}));

const Commande = (props) => {
  const {carId} = useParams();
	const history = useHistory();
  const classes = useStyles();
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState(false);
  const [login, setLogin] = React.useState(false);
	const [car, setCar] = useState();
	const [commande, setCommande] = useState({});

  React.useEffect(()=>{
    console.log('commande update')
    if(props.user && login === true && commande !== {}){
      console.log('set send commande after login')
      sendCommande({
        client: props.user.id,
        ...commande
      })
    }
  });

  const {baseURL} = props;
  React.useEffect(() =>{
    console.log('car')
		axiosInstance
    .get(`${baseURL}client-api/car-list/${carId}/`)
    .then((res) => {
      console.log('je commande car ')
      setCar(res.data[0]);
      setLoading(false);
    }).catch((res) => {
    });
  }, [baseURL, carId])

  const setDateTime = (data)=>{
    console.log('je suis dans ');
    console.log(data);
    setLoading(true);
    if(props.user){
      sendCommande({
        client: props.user.id,
        car: carId,
        start_location: data.startDate.toJSON(),
        end_location: data.endDate.toJSON()
      })
    }else{
      setLogin(true)
      setCommande({
        car: carId,
        start_location: data.startDate.toJSON(),
        end_location: data.endDate.toJSON()
      })
    }
  }

  const sendCommande = (data) =>{
    axiosInstance.post(`${baseURL}client-api/commande-car/`, data)
    .then((res) => {
      console.log(res.data);
      history.push('/compte');
    })
    .catch((error)=>{
      setLoading(false);
      setError(true);
    });
  }

  return (
    <div>
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      <Grid item xs={12} sm={5} md={5} className={classes.image} >
        <Grid container direction="column" justify="center" alignItems="center" className={classes.header} >
          <div elevation={0}>
            {
              car 
              ? <>
              <Grid container direction="row" justify="center" alignItems="center">
                <Grid item xs={12} sm={12} md={12} lg={12} container direction="row" justify="center" alignItems="center">
                  <img src={car.image} alt="véhicule" />
                  <div className='price-container'>
                    {car.promo ? <div className='promo'><NewReleasesRoundedIcon style={{fontSize: '60px !important'}}/> Promo</div> : null }
                     <div className='price'>
                        {car.price} <sup>$</sup>/jour
                      </div>
                  <Typography component="h5" variant="h5" color='primary' style={{textAlign: 'center'}} >
                    {car.name}
                  </Typography>
                  </div>
                </Grid>

                <Grid item xs={12} sm={12} md={12} lg={12} >
                  <Divider />
                  <div className={classes.details}>
                  <CardContent className={classes.content}>

                    <Grid container direction="row" justify="center" spacing={2}>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        {car.description ?<Typography style={{textAlign: 'center'}}>
                        {car.description}
                        </Typography> : null}
                      </Grid>
                      <Grid item xs={12} sm={12} md={12} lg={12}>
                        {car.climatisation ?<Typography  color="textSecondary">
                          <AcUnitRoundedIcon color='primary'/> Climatisation
                          </Typography> : null}
                        {car.boite ? <Typography color="textSecondary" >
                          <DoneAllRoundedIcon color='primary'/> Boite automatique
                          </Typography> : null}
          
                        <Grid container justify="center">
                          <Grid item xs={4} sm={4} md={4} lg={4}>
                            <Typography color="textSecondary">
                              {car.porte} Portes 
                            </Typography>
                            <Typography color="textSecondary">
                              {car.place} Places
                            </Typography>
                          </Grid>
                          <Grid item xs={4} sm={6} md={6} lg={6}>
                            <Typography color="textSecondary">
                              {car.sac} Sacs
                            </Typography>
                            <Typography color="textSecondary">
                              {car.valise} valises 
                            </Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </CardContent>
        
      </div>
      </Grid>
              </Grid>
              </>
              : <CircularProgress size={24} className={classes.buttonProgress} />}
          </div>

        </Grid>
      </Grid>
      <Grid item xs={12} sm={7} md={7} component={Paper} elevation={6} square>
        <div className={classes.paper}>
          {
          login 
            ? <Login setLogin={setLogin} handleUserAuth={props.handleUserAuth}/>
            : <DateTimePicker setDateTime={setDateTime} loading={loading} error={error}/> 
          }
        </div>
      </Grid>
    </Grid>
  </div>
  );
}

export default Commande;